.settings-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.settings-panel {
  background-color: var(--bg-color);
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  padding: 1.5rem;
  color: var(--text-color);
}

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 1rem;
}

.settings-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: var(--heading-color);
}

.close-button {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--text-color);
}

.settings-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.settings-section {
  margin-bottom: 1.5rem;
}

.settings-section h3 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  color: var(--heading-color);
  border-bottom: 1px solid var(--border-color-light);
  padding-bottom: 0.5rem;
}

.settings-option {
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
}

.settings-option label {
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.settings-option select,
.settings-option input[type="text"],
.settings-option input[type="number"] {
  padding: 0.5rem;
  border-radius: 4px;
  border: 1px solid var(--border-color);
  background-color: var(--input-bg);
  color: var(--text-color);
  font-size: 0.9rem;
}

.settings-option input[type="color"] {
  width: 100%;
  height: 40px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background-color: var(--input-bg);
}

.range-container {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.range-container input[type="range"] {
  flex: 1;
}

.range-container span {
  min-width: 3rem;
  text-align: right;
}

.theme-selector,
.columns-selector {
  display: flex;
  gap: 0.5rem;
}

.theme-button,
.column-button {
  padding: 0.5rem 1rem;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background-color: var(--input-bg);
  color: var(--text-color);
  cursor: pointer;
  transition: all 0.2s ease;
}

.theme-button.active,
.column-button.active {
  background-color: var(--accent-color);
  color: white;
  border-color: var(--accent-color);
}

.settings-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-color);
}

.reset-button,
.save-button {
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.reset-button {
  background-color: transparent;
  border: 1px solid var(--border-color);
  color: var(--text-color);
}

.save-button {
  background-color: var(--accent-color);
  border: 1px solid var(--accent-color);
  color: white;
}

.reset-button:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.save-button:hover {
  background-color: var(--accent-color-dark);
}

/* Адаптивность для мобильных устройств */
@media (max-width: 768px) {
  .settings-content {
    grid-template-columns: 1fr;
  }
  
  .settings-panel {
    width: 95%;
    padding: 1rem;
  }
  
  .theme-selector,
  .columns-selector {
    flex-wrap: wrap;
  }
  
  .theme-button,
  .column-button {
    flex: 1;
    min-width: 60px;
    text-align: center;
  }
}

/* Стили для темной темы */
.dark-theme .settings-panel {
  background-color: var(--bg-color-dark);
  color: var(--text-color-dark);
}

.dark-theme .settings-header h2,
.dark-theme .settings-section h3 {
  color: var(--heading-color-dark);
}

.dark-theme .close-button {
  color: var(--text-color-dark);
}

.dark-theme .settings-option select,
.dark-theme .settings-option input[type="text"],
.dark-theme .settings-option input[type="number"] {
  background-color: var(--input-bg-dark);
  color: var(--text-color-dark);
  border-color: var(--border-color-dark);
}

.dark-theme .theme-button,
.dark-theme .column-button {
  background-color: var(--input-bg-dark);
  color: var(--text-color-dark);
  border-color: var(--border-color-dark);
}

.dark-theme .reset-button {
  background-color: transparent;
  border-color: var(--border-color-dark);
  color: var(--text-color-dark);
}

.dark-theme .reset-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

/* Режим высокой контрастности */
.high-contrast {
  --text-color: #000000;
  --text-color-dark: #ffffff;
  --bg-color: #ffffff;
  --bg-color-dark: #000000;
  --heading-color: #000000;
  --heading-color-dark: #ffffff;
  --border-color: #000000;
  --border-color-dark: #ffffff;
  --input-bg: #f0f0f0;
  --input-bg-dark: #333333;
}

.high-contrast .theme-button.active,
.high-contrast .column-button.active,
.high-contrast .save-button {
  background-color: #000000;
  color: #ffffff;
  border-color: #000000;
}

.dark-theme.high-contrast .theme-button.active,
.dark-theme.high-contrast .column-button.active,
.dark-theme.high-contrast .save-button {
  background-color: #ffffff;
  color: #000000;
  border-color: #ffffff;
}
