/* Theme styles for catalog */

:root {
  --primary-color: #0099ff;
  --primary-dark: #0077cc;
  --secondary-color: #f0f0f0;
  --text-color: #333;
  --text-light: #777;
  --border-color: #ddd;
  --background-color: #fff;
  --card-background: #fff;
  --hover-color: #f5f5f5;
  --success-color: #4CAF50;
  --warning-color: #FFC107;
  --info-color: #2196F3;
  --danger-color: #ff3333;
  --shadow-color: rgba(0, 0, 0, 0.1);
  --modal-overlay: rgba(0, 0, 0, 0.5);
}

body.dark-theme {
  --primary-color: #0099ff;
  --primary-dark: #0077cc;
  --secondary-color: #444;
  --text-color: #eee;
  --text-light: #aaa;
  --border-color: #555;
  --background-color: #222;
  --card-background: #333;
  --hover-color: #444;
  --success-color: #4CAF50;
  --warning-color: #FFC107;
  --info-color: #2196F3;
  --danger-color: #ff3333;
  --shadow-color: rgba(0, 0, 0, 0.3);
  --modal-overlay: rgba(0, 0, 0, 0.7);
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s, color 0.3s;
}

.catalog-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.catalog-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--border-color);
  text-align: center;
}

.catalog-header h1 {
  margin: 0 0 15px 0;
  font-size: 2rem;
  color: var(--primary-color);
}

.catalog-actions-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  width: 100%;
  margin-bottom: 20px;
}

.action-button {
  padding: 8px 15px;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 5px;
}

.action-button:hover {
  background-color: var(--primary-dark);
}

.action-button.settings-button {
  background-color: var(--secondary-color);
  color: var(--text-color);
}

.action-button.settings-button:hover {
  background-color: var(--hover-color);
}

.action-button.bulk-button {
  background-color: var(--secondary-color);
  color: var(--text-color);
}

.action-button.bulk-button.active {
  background-color: var(--primary-color);
  color: white;
}

.export-dropdown {
  position: relative;
  display: inline-block;
}

.export-button {
  background-color: var(--success-color);
}

.export-button:hover {
  background-color: #388E3C;
}

.export-dropdown-content {
  display: none;
  position: absolute;
  background-color: var(--card-background);
  min-width: 160px;
  box-shadow: 0 8px 16px 0 var(--shadow-color);
  z-index: 1;
  border-radius: 4px;
  overflow: hidden;
}

.export-dropdown-content button {
  color: var(--text-color);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  width: 100%;
  text-align: left;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: background-color 0.3s;
}

.export-dropdown-content button:hover {
  background-color: var(--hover-color);
}

.export-dropdown:hover .export-dropdown-content {
  display: block;
}

.display-mode-selector {
  display: flex;
  gap: 5px;
  margin-left: 10px;
}

.display-mode-button {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 8px 12px;
  background-color: var(--secondary-color);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
  color: var(--text-color);
}

.display-mode-button:hover {
  background-color: var(--hover-color);
}

.display-mode-button.active {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-dark);
}

.mode-icon {
  font-size: 1.2rem;
}

.mode-label {
  font-size: 0.9rem;
}

.catalog-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 20px;
}

.search-filter {
  flex: 1 1 300px;
}

.search-filter input {
  width: 100%;
  padding: 10px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background-color: var(--card-background);
  color: var(--text-color);
  transition: border-color 0.3s, box-shadow 0.3s;
}

.search-filter input:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(0, 153, 255, 0.2);
  outline: none;
}

.filter-group {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  flex: 1 1 300px;
}

.filter-item {
  flex: 1 1 150px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.filter-item label {
  font-weight: 500;
  font-size: 0.9rem;
}

.filter-dropdown {
  display: flex;
  align-items: center;
  gap: 5px;
}

.filter-dropdown select {
  flex: 1;
  padding: 9px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background-color: var(--card-background);
  color: var(--text-color);
  transition: border-color 0.3s, box-shadow 0.3s;
}

.filter-dropdown select:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(0, 153, 255, 0.2);
  outline: none;
}

.category-actions, .status-actions {
  display: flex;
  gap: 2px;
}

.small-button {
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--border-color);
  background-color: var(--secondary-color);
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 1rem;
  padding: 0;
}

.small-button:hover {
  background-color: var(--hover-color);
}

.small-button.add-button {
  color: var(--success-color);
}

.small-button.edit-button {
  color: var(--info-color);
}

.small-button.delete-button {
  color: var(--danger-color);
}

.rubrics-container, .tags-filter-container {
  margin-bottom: 20px;
}

.rubrics-container h3, .tags-filter-container h3 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1.1rem;
  color: var(--text-color);
}

.rubrics-buttons, .tags-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.rubric-button, .tag-button {
  padding: 6px 12px;
  background-color: var(--secondary-color);
  border: 1px solid var(--border-color);
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 0.9rem;
  color: var(--text-color);
}

.rubric-button:hover, .tag-button:hover {
  background-color: var(--hover-color);
}

.rubric-button.active, .tag-button.active {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-dark);
}

.tag-button.add-tag {
  background-color: transparent;
  border: 1px dashed var(--border-color);
  color: var(--text-light);
}

.tag-button.add-tag:hover {
  background-color: var(--hover-color);
  color: var(--text-color);
}

.catalog-stats {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px;
  background-color: var(--secondary-color);
  border-radius: 4px;
  font-size: 0.9rem;
  color: var(--text-color);
}

.bulk-actions {
  margin-bottom: 20px;
  padding: 15px;
  background-color: var(--secondary-color);
  border-radius: 4px;
  animation: highlight 2s ease-in-out;
}

@keyframes highlight {
  0% { background-color: rgba(0, 153, 255, 0.2); }
  100% { background-color: var(--secondary-color); }
}

.bulk-controls {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
}

.selected-count {
  font-weight: bold;
  color: var(--primary-color);
  margin-right: 5px;
}

.bulk-select-all {
  padding: 6px 12px;
  background-color: var(--secondary-color);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
  color: var(--text-color);
}

.bulk-select-all:hover {
  background-color: var(--hover-color);
}

.bulk-category-select, .bulk-status-select {
  padding: 6px 10px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background-color: var(--card-background);
  color: var(--text-color);
  transition: border-color 0.3s, box-shadow 0.3s;
}

.bulk-category-select:focus, .bulk-status-select:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(0, 153, 255, 0.2);
  outline: none;
}

.bulk-delete {
  padding: 6px 12px;
  background-color: var(--danger-color);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.bulk-delete:hover {
  background-color: #cc0000;
}

.prompts-container {
  margin-bottom: 30px;
}

.prompts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.prompts-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.prompt-card {
  position: relative;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 15px;
  background-color: var(--card-background);
  box-shadow: 0 2px 4px var(--shadow-color);
  transition: all 0.3s ease;
}

.prompts-grid .prompt-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px var(--shadow-color);
}

.prompts-list .prompt-card:hover {
  transform: translateX(5px);
  box-shadow: 0 2px 8px var(--shadow-color);
}

.prompt-card.selected {
  border: 2px solid var(--primary-color);
  background-color: rgba(0, 153, 255, 0.05);
  box-shadow: 0 0 0 3px rgba(0, 153, 255, 0.3);
}

.selection-indicator {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 2;
}

.selection-indicator input[type="checkbox"] {
  width: 20px;
  height: 20px;
  cursor: pointer;
  accent-color: var(--primary-color);
}

.prompt-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 10px;
}

.prompt-header h3 {
  margin: 0;
  font-size: 1.2rem;
  line-height: 1.4;
  max-width: 80%;
  color: var(--text-color);
}

.status-badge {
  padding: 3px 8px;
  border-radius: 12px;
  font-size: 0.8rem;
  background-color: var(--secondary-color);
  color: var(--text-color);
}

.status-badge.готов {
  background-color: var(--success-color);
  color: white;
}

.status-badge.тест {
  background-color: var(--warning-color);
  color: black;
}

.status-badge.в\ работе {
  background-color: var(--info-color);
  color: white;
}

.prompt-description {
  margin-bottom: 15px;
  font-size: 0.9rem;
  color: var(--text-light);
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: 1.5;
}

.prompt-category {
  margin-bottom: 10px;
  font-size: 0.9rem;
  color: var(--text-color);
}

.prompt-tags {
  margin-bottom: 15px;
  font-size: 0.9rem;
}

.tags-list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  margin-top: 5px;
}

.tag {
  padding: 2px 8px;
  background-color: var(--secondary-color);
  border-radius: 12px;
  font-size: 0.8rem;
  color: var(--text-color);
}

.prompt-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.details-button {
  padding: 6px 12px;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 0.9rem;
}

.details-button:hover {
  background-color: var(--primary-dark);
}

.action-icons {
  display: flex;
  gap: 5px;
}

.icon-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 5px;
  border-radius: 50%;
  transition: background-color 0.2s;
}

.icon-button:hover {
  background-color: var(--secondary-color);
}

.pagination-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 5px;
  margin: 30px 0;
}

.pagination-button {
  min-width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 8px;
  border: 1px solid var(--border-color);
  background-color: var(--card-background);
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.3s;
  font-size: 0.9rem;
  color: var(--text-color);
}

.pagination-button.active {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-dark);
  font-weight: bold;
}

.pagination-button.ellipsis {
  cursor: default;
  background-color: transparent;
  border-color: transparent;
}

.pagination-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.pagination-button:not(:disabled):hover {
  background-color: var(--hover-color);
  border-color: var(--border-color);
}

.pagination-button.active:hover {
  background-color: var(--primary-dark);
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--modal-overlay);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(3px);
}

.modal-content {
  position: relative;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  background-color: var(--card-background);
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 5px 15px var(--shadow-color);
}

.small-modal {
  max-width: 500px;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--border-color);
}

.modal-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: var(--text-color);
}

.modal-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  transition: transform 0.2s;
  color: var(--text-color);
}

.modal-close:hover {
  transform: scale(1.1);
}

.modal-body {
  margin-bottom: 20px;
}

.edit-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.form-group label {
  font-weight: 500;
  color: var(--text-color);
}

.form-group input, 
.form-group textarea, 
.form-group select {
  padding: 10px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background-color: var(--card-background);
  color: var(--text-color);
  transition: border-color 0.3s, box-shadow 0.3s;
}

.form-group input:focus, 
.form-group textarea:focus, 
.form-group select:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(0, 153, 255, 0.2);
  outline: none;
}

.prompt-details {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.prompt-text {
  margin-bottom: 20px;
}

.prompt-text h3, 
.prompt-info h3 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1.2rem;
  color: var(--text-color);
}

.text-content {
  white-space: pre-wrap;
  padding: 15px;
  background-color: var(--secondary-color);
  border-radius: 4px;
  margin-bottom: 15px;
  line-height: 1.5;
  color: var(--text-color);
}

.prompt-info {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.info-section, .tags-section {
  padding: 15px;
  background-color: var(--secondary-color);
  border-radius: 4px;
}

.info-item {
  display: flex;
  margin-bottom: 10px;
}

.label {
  font-weight: bold;
  margin-right: 10px;
  min-width: 100px;
  color: var(--text-color);
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  padding-top: 15px;
  border-top: 1px solid var(--border-color);
}

.save-button {
  padding: 8px 15px;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.save-button:hover {
  background-color: var(--primary-dark);
}

.cancel-button, .close-button {
  padding: 8px 15px;
  background-color: var(--secondary-color);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.cancel-button:hover, .close-button:hover {
  background-color: var(--hover-color);
}

.edit-button {
  padding: 8px 15px;
  background-color: var(--info-color);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.edit-button:hover {
  background-color: #0b7dda;
}

.no-prompts-message {
  padding: 30px;
  text-align: center;
  background-color: var(--secondary-color);
  border-radius: 8px;
  margin-bottom: 30px;
  color: var(--text-color);
}

/* Animation for card selection */
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(0, 153, 255, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(0, 153, 255, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(0, 153, 255, 0);
  }
}

.prompt-card.selected {
  animation: pulse 2s infinite;
}

/* Improved checkbox styling */
.selection-indicator input[type="checkbox"] {
  appearance: none;
  -webkit-appearance: none;
  width: 22px;
  height: 22px;
  border: 2px solid var(--border-color);
  border-radius: 4px;
  outline: none;
  transition: all 0.3s;
  position: relative;
  cursor: pointer;
  background-color: var(--card-background);
}

.selection-indicator input[type="checkbox"]:checked {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

.selection-indicator input[type="checkbox"]:checked::after {
  content: '✓';
  position: absolute;
  color: white;
  font-size: 16px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Improved focus states for accessibility */
button:focus, 
input:focus, 
select:focus, 
textarea:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 153, 255, 0.3);
}

/* Improved hover states */
.prompt-card:hover .action-icons {
  opacity: 1;
}

.action-icons {
  opacity: 0.7;
  transition: opacity 0.3s;
}
